<template>
  <div>
    <div>
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <!-- <el-form-item>
          <el-select v-model="formInline.region" placeholder="请输入用户类型">
            <el-option label="普通账号" value="user"></el-option>
            <el-option label="评估师账户" value="Appraiser"></el-option>
          </el-select>
        </el-form-item>-->
        <el-form-item>
          <el-button type="primary" @click="onSubmit">注册评估师<br></el-button>
          <el-button type="primary" @click="logout">注销</el-button>
          <el-button type="primary" @click="check">查看</el-button>
        </el-form-item>
        <el-form-item>
          <el-slider v-model="value" :format-tooltip="formatTooltip" :step="5"  show-stops></el-slider>
          <el-button type="primary" @click="setparameter">修改订单提成</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import token from "@/js/token";
import login from "@/js/login";

export default {
  data() {
    return {
      formInline: {
        user: "",
        region: ""
      },
      value: 50
    };
  },
  created: async function() {
    await token.init_token();
    await login.init_user();
  },
  methods: {
    async onSubmit() {
      this.$router.push({name: 'register',params:{ id:'1'}});
    },
    async logout() {
      await login.unregister();
    },
    async check() {
      console.log(await login.checkuser());
    },
    async setparameter() {
      // this.$prompt("请输入报价", "提示", {
      //   confirmButtonText: "确定",
      //   cancelButtonText: "取消"
      // }).then(async ({ value }) => {
      //   await login.Enter_quote(value);
      // });
      var commission = 200 - this.value;
      console.log(commission);
      await login.Enter_quote(commission);
    },
    formatTooltip(val) {
        return (val + "%");
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



